  .Auth-form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background: linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) ), url("https://falconhelitours.com/wp-content/uploads/2021/09/FTPhotographyA14-2-1536x1024.jpg") no-repeat center fixed;
    background-size: cover;
  }
  
  .Authform {
    box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px !important;
    background: linear-gradient(to right, #4568dc, #b06ab3) !important;
    opacity: 0.9;
  }
  
  .Auth-form-content {
    padding-left: 8%;
    padding-right: 8%;
  }
  
  .Auth-form-title {
    text-align: center;
    margin-bottom: 1em;
    font-size: 24px;
    color: rgb(34, 34, 34);
    font-weight: 800;
  }
  
  label {
    font-size: 15px;
    font-weight: 400;
    color: #ffffff !important;
  }

  #Card {
    box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px !important;
    background: linear-gradient(to right, #4568dc, #b06ab3) !important;
    opacity: 0.4 !important;
  }

  .btnfc {
    background: #ad1f2f !important;
    border: 1px solid #ad1f2f !important;
}